import React,{ useContext, useEffect } from 'react';
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';

import { ThemeContext } from '../../contexts/ThemeContext';
import { projectsData } from '../../data/projectsData';
import { HiArrowRight } from "react-icons/hi";
import AOS from 'aos';
import "aos/dist/aos.css";

import './Projects.css'
import SingleProject from './SingleProject/SingleProject';

function Projects() {

    const { theme } = useContext(ThemeContext);
    
    const useStyles = makeStyles(() => ({
        viewAllBtn : {
            color: theme.primary, 
            backgroundColor: theme.tertiary,
            transition: 'color 0.2s',
            "&:hover": {
                color: theme.secondary, 
                backgroundColor: theme.primary80,
            }
        },
        viewArr : {
            color: theme.tertiary, 
            backgroundColor: theme.secondary70,
            width: '40px',
            height: '40px',
            padding: '0.5rem',
            fontSize: '1.05rem',
            borderRadius: '50%',
            cursor: 'pointer',
            transition: 'background-color 0.2s',
            "&:hover": {
                color: theme.primary, 
                backgroundColor: theme.secondary,
            }
        },
    }));

    const classes = useStyles();

    useEffect(() => {
        AOS.init({});
    }, []);

    return (
        <>
            {projectsData.length > 0 && (
                <div className="projects" id="projects" style={{backgroundColor: theme.primary}}>
                    <div className="projects--header">
                        <h1 style={{color: theme.secondary}}>Projects</h1>
                    </div>
                    <div className="projects--body" data-aos="fade-up">
                        <div className="projects--bodyContainer">
                            {projectsData.slice(0, 3).map(project => (
                                <SingleProject
                                    theme={theme}
                                    key={project.id}
                                    id={project.id}
                                    name={project.projectName}
                                    desc={project.projectDesc}
                                    tags={project.tags}
                                    code={project.code}
                                    demo={project.demo}
                                    image={project.image}
                                />
                            ))}
                        </div> 

                        {/* {projectsData.length > 3 && ( */}
                            <div className="projects--viewAll" data-aos="fade-left">
                                <a href="https://github.com/mollyy0514" target='_blank'>
                                    <button className={classes.viewAllBtn}>
                                        View more
                                        <HiArrowRight className={classes.viewArr} />
                                    </button>
                                </a>
                            </div>
                        {/* )} */}
                    </div>
                </div>
            )}

        </>
    )
}

export default Projects
