// 命名index.js的話，App.js裡import就只需要"import NavBar from './components/NavBar';"
import { useEffect, useState, useContext } from 'react';
import { IconContext } from 'react-icons/lib';
import { FaBars } from 'react-icons/fa';
import { animateScroll as scroll } from 'react-scroll/modules';
import { Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItem, NavLinks, NavBtn, NavBtnLink } from './NavBarElements';
import { headerData } from '../../data/headerData';
import { ThemeContext } from '../../contexts/ThemeContext';

export default function Navbar({ toggle }) {

    const [scrollNav, setScrollNav] = useState(false);
    const { theme } = useContext(ThemeContext);

    const changeNav = () => {
        if (window.scrollY >= 800) {
            setScrollNav(true);
        }
        else {
            setScrollNav(false);
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav);
    }, [])

    // react scroll
    const toggleHome = () => {
        scroll.scrollToTop();
    }

    const shortname = (name) => {
        if (name.length > 12) {
            return name.split(' ')[0];
        } else {
            return name;
        }
    };

    return (
        <>
        {/* <IconContext.Provider value={{color: 'red'}}> */}
            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                    <NavLogo style={{ color: theme.primary, fontFamily: 'BestermindRegular' }}>
                        {shortname(headerData.nickname)}
                    </NavLogo>
                    <MobileIcon onClick={toggle}>
                        <FaBars />
                    </MobileIcon>
                    <NavMenu>
                        {/* react scroll */}
                        <NavItem>
                            <NavLinks to="about" smooth={true} duration={500} spy={true} offset={-80}>About</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="skills" smooth={true} duration={500} spy={true} offset={-80}>Skills</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="projects" smooth={true} duration={500} spy={true} offset={-80}>Projects</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="blog" smooth={true} duration={500} spy={true} offset={-80}>Publications</NavLinks>
                        </NavItem>
                    </NavMenu>
                </NavbarContainer>
            </Nav>
        {/* </IconContext.Provider> */}
        </>
    )
}
